<template>
    <div class="m-publish-box">
        <!--  -->
        <publish-header name="">
            <slot name="header"></slot>
        </publish-header>

        <el-form class="m-publish-post">
            <div class="m-publish-source">
                <el-divider content-position="left"> *</el-divider>
                <el-radio-group class="m-publish-action" v-model="action">
                    <el-radio-button label="new"></el-radio-button>
                    <el-radio-button label="update"></el-radio-button>
                </el-radio-group>
                <el-select
                    v-if="!isNew"
                    class="u-source-id"
                    v-model="post.source_id"
                    filterable
                    remote
                    :disabled="!!post.id"
                    placeholder=""
                    :remote-method="search_handle"
                    :loading="options.loading"
                    allow-create
                    clearable
                    @clear="handleClear"
                    @change="handleChange"
                >
                    <el-option v-for="item in options.sources" :key="item.id" :label="item.name" :value="item.id">
                        <div class="u-option">
                            <span class="u-type" v-text="item.label"></span>
                            <span class="u-name" v-text="item.name"></span>
                        </div>
                    </el-option>
                </el-select>
                <template v-if="isNew">
                    <div class="u-source-top">
                        <el-select v-if="types" class="u-source-type" placeholder="" v-model="knowledge.type">
                            <el-option v-for="type in types" :key="type.name" :value="type.name" :label="type.label"></el-option>
                        </el-select>
                        <el-input class="u-source-name" placeholder="" v-model="knowledge.name"></el-input>
                    </div>
                    <p class="m-knowledge-add">
                        <!-- <span></span> -->
                        <!-- <el-button
                            type="success"
                            @click="dialogVisible = true"
                            size="mini"
                            ></el-button
                        > -->
                        <span>Note</span>
                    </p>
                </template>
            </div>

            <div class="m-publish-remark">
                <el-divider content-position="left"> *</el-divider>
                <el-input v-model="post.remark" :maxlength="200" :minlength="1" show-word-limit required placeholder=""></el-input>
            </div>

            <div class="m-publish-content">
                <el-divider content-position="left"> *</el-divider>
                <Tinymce v-model="post.content" :attachmentEnable="true" :resourceEnable="true" :height="600" />
            </div>

            <div class="m-publish-tags">
                <el-divider content-position="left"></el-divider>
                <el-tag :key="key" v-for="(tag, key) in post.tags" closable :disable-transitions="false" @close="post.tags.splice(key, 1)">{{ tag }} </el-tag>
                <el-input
                    class="input-new-tag"
                    v-if="inputVisible"
                    v-model="inputValue"
                    ref="saveTagInput"
                    size="small"
                    placeholder=""
                    @keyup.enter.native="handleInputConfirm"
                    @blur="handleInputConfirm"
                >
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput">+  </el-button>
            </div>

            <div class="m-publish-commit">
                <el-divider content-position="left"></el-divider>
                <el-button class="u-publish" icon="el-icon-s-promotion" type="primary" @click="handleSubmit" :disabled="processing"> </el-button>
            </div>
        </el-form>

        <!--  -->
        <el-dialog title="" :visible.sync="dialogVisible" width="640px" @open="knowledge = { type: '', name: '' }">
            <el-form class="m-publish-knowledge" v-if="knowledge">
                <div class="m-publish-type">
                    <el-divider content-position="left"> *</el-divider>
                    <el-radio-group v-if="types" v-model="knowledge.type">
                        <el-radio :label="key" border v-for="(type, key) in types" :key="type.name">{{ type.label }} </el-radio>
                    </el-radio-group>
                </div>

                <div class="m-publish-name">
                    <el-divider content-position="left"> *</el-divider>
                    <el-input v-model="knowledge.name" placeholder="" :minlength="1" :maxlength="50" show-word-limit></el-input>
                </div>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"></el-button>
                <el-button type="primary" :disabled="processing" @click="create_knowledge"></el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import header from "@/components/publish_header.vue";
import Tinymce from "@jx3box/jx3box-editor/src/Tinymce";

// 
import { WikiPost } from "@jx3box/jx3box-common/js/helper";
import User from "@jx3box/jx3box-common/js/user";
import { get_menus, get_list, create_knowledge } from "../service/knowledge";
import cloneDeep from "lodash/cloneDeep";

export default {
    name: "knowledge",
    data() {
        return {
            dialogVisible: false,
            knowledge: {
                type: "",
                name: "",
            },
            post: {
                id: "",
                content: "",
                source_id: "",
                remark: "",
                tags: [],
            },
            types: null,
            options: {
                sources: null,
                loading: false,
            },
            inputVisible: false,
            inputValue: "",

            processing: false,

            // isNew: false, // 
            copySource: [],

            // 
            action: "new",
        };
    },
    computed: {
        isNew() {
            return this.action === "new";
        },
    },
    methods: {
        create_knowledge() {
            this.processing = true;
            return create_knowledge({
                type: this.knowledge.type,
                name: this.knowledge.name,
                client : 'all'
            })
                .then((res) => {
                    res = res.data;
                    if (res.code === 200) {
                        // 
                        this.post.source_id = res.data.id;
                        // this.$message({ message: "", type: "success" });
                        // this.dialogVisible = false;
                    } else {
                        this.$message({
                            message: `${res.message}`,
                            type: "warning",
                        });
                    }
                })
                .finally(() => {
                    this.processing = false;
                });
        },
        // 
        search_handle(keyword = "") {
            this.options.loading = true;
            get_list({
                keyword: keyword,
                include_pending: 1,
                limit: 10,
            }).then((res) => {
                res = res.data;
                if (res.code === 200) this.options.sources = res.data.data;

                if (res.data.data.length) {
                    this.copySource = cloneDeep(res.data.data);
                }
                this.options.loading = false;

                // 
                /* if (!this.options.sources.length) {
                    this.isNew = true
                } else {
                    this.isNew = false
                } */

                // 
                if (!keyword) {
                    this.options.sources = this.copySource;
                }
            });
        },
        validate() {
            let result = ["source_id", "content", "remark"].every((prop) => {
                return !!this.post[prop];
            });
            if (!result) this.$message.error("");
            return result;
        },
        handleSubmit: function() {
            if (this.isNew) {
                this.create_knowledge().then(() => {
                    this.publish();
                });
            } else {
                this.publish();
            }
        },
        publish: function() {
            // 
            if (!this.validate()) return;
            this.processing = true;
            WikiPost.save({
                type: "knowledge",
                source_id: this.post.source_id,
                user_nickname: User.getInfo().name,
                content: this.post.content,
                remark: this.post.remark,
                tags: this.post.tags,
            })
                .then((res) => {
                    res = res.data;
                    if (res.code === 200) {
                        this.$message({ message: "", type: "success" });
                        setTimeout(() => {
                            this.$router.push({ name: "wiki_post", params: { type: "knowledge" } });
                        }, 500);
                    } else {
                        this.$message({ message: `${res.message}`, type: "warning" });
                    }
                })
                .finally(() => {
                    this.processing = false;
                });
        },
        showInput() {
            this.inputVisible = true;
            this.$nextTick((_) => {
                this.$refs.saveTagInput.$refs.input.focus();
            });
        },
        handleInputConfirm() {
            let inputValue = this.inputValue.trim();
            if (inputValue) {
                this.post.tags.push(inputValue);
            }
            this.inputVisible = false;
            this.inputValue = "";
        },
        handleChange: function(val) {
            this.knowledge.name = val;
            this.post.source_id = val;
        },
        handleClear: function() {
            this.options.sources = this.copySource;
        },
    },
    created() {
        // 
        this.search_handle("");
        // IDwatch
        let id = parseInt(this.$route.params.source_id);
        this.post.source_id = id || "";

        get_menus().then((res) => {
            res = res.data;
            if (res.code === 200) this.types = res.data.menus;
        });
    },
    watch: {
        "post.source_id": {
            handler() {
                if (!this.post.source_id) {
                    this.post = this.$options.data().post;
                } else {
                    WikiPost.newest("knowledge", this.post.source_id, 0).then((res) => {
                        let data = res.data;
                        if (data.code === 200) {
                            // 
                            let post = data.data.post;
                            let source = data.data.source;

                            if (post) {
                                this.post.source_id = parseInt(post.source_id);
                                this.post.remark = post.remark;
                                this.post.content = post.content;
                                this.post.tags = post.tags || [];
                            } else {
                                this.post.source_id = this.post.source_id;
                                this.post.remark = "";
                                this.post.content = "";
                                this.post.tags = [];
                            }

                            if (source) {
                                // 
                                let exist = false;
                                this.options.sources = this.options.sources || [];
                                for (let index in this.options.sources) {
                                    if (this.options.sources[index].id == source.id) {
                                        exist = true;
                                        break;
                                    }
                                }
                                if (!exist) this.options.sources.push(source);
                            }
                        }
                    });
                }
            },
        },
        action: function() {
            this.knowledge = this.$options.data().knowledge;
            this.post = this.$options.data().post;
        },
    },
    components: {
        "publish-header": header,
        Tinymce,
    },
};
</script>

<style scoped lang="less">
@import "../assets/css/knowledge.less";
</style>
